* {
  margin: 0;
  padding: 0;
}

.button {
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.button--active {
  color: black;
  padding: 15px 32px;
  border: none;
}
